@charset "utf-8";
/* App.css */

/* 웹폰트 */
@import url('https://cdn.jsdelivr.net/gh/orioncactus/pretendard/dist/web/static/pretendard.css');

*{
  padding: 0; margin: 0;
  text-decoration: none;
  list-style: none;
}

body {
  font-family: 'Pretendard';
  font-size: 12px;
  color: #333;
  height: 100%;
  background: #eff3f7;
}

.loading-bg {
  width: 100%; height: 100vh;
  position: fixed; top:0px; left:0px;
  background: #000;
}
.load_img {
  position: absolute;
  top: 50%; left: 50%;
  transform: translate(-50%, -50%);
  }

.nav {
  text-align: center;
  background: #6ac143;
  margin-bottom: 30px;
  border-radius: 5px;
  box-sizing: border-box;
  margin-left: 20px;
}

.nav li {
  padding: 15px 10px;
  border-bottom: 1px solid #ffffff33;
}

.nav li a {
  font-size: 16px;
  color: #fff;
}

/* pc버전 */
@media screen and (min-width: 768px) {
  /* 헤더 */
  header {
    position: fixed;
    left: 0px; top: 0px;
    background: #eff3f7;
    width: 180px; height: 100vh;
  }
  header > h1 {
    text-align: center;
    padding: 15px 0;
  }
  header h1 img {
    width: 120px;
  }
  /* 메인 */
  main {
    position: absolute;
    left: 180px; top: 0px;
    width: calc(100% - 180px);
    padding: 30px;
    padding-top: 60px;
    box-sizing: border-box;
  }
}


/* 모바일 버전 */
@media screen and (max-width: 767px) {
  header {
    position: absolute;
    width: 100%; top: 0px;
    background: #eff3f7;
  }
  header > h1 {
    text-align: center;
    margin: 15px 0;
  }
  header h1 img {
    width: 100px;
  }
  main {
    padding: 14px;
    padding-top: 340px;
  }
}
