@charset "utf-8";

/* movie.css */
.movie {
  width: 47%;
  background: #fff;
  padding: 30px 20px;
  margin-bottom: 60px;
  margin-left: 2%;
  box-shadow: 0px 0px 10px #ddd;
  box-sizing: border-box;
  display: inline-block;
}
.movie img {
  max-width: 150px;
  width: 100%;
  margin-right: 30px;
  position: relative; top: -60px;
  box-shadow: 0px 20px 20px -12px #888;
  transition: 0.5s;
}
.movie img:hover{
  transform: perspective(600px) rotateX(20deg);
}
.movie_data{}
.movie_title{
  font-size: 20px;
  margin-top: -40px;
  margin-bottom: 5px;
  color: #2c2c2c;
  font-weight: 400;
}
.movie_year{
  font-weight: 300;
  font-size: 14px;
}
.movie_genres{
  display: flex;
  flex-wrap: wrap;
  margin: 5px 0px;
}
.movie_genres_txt{
  margin-right: 10px;
  font-size: 14px;
}
.movie_summary{
  margin-top: 10px;
}